.container {
  max-width: 700px;
  min-width: 360px;
  margin: 0 auto;
  padding: 12px 0;
  position: relative;
  min-height: 640px;
  height: 100vh;
}

.header {
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 12px;
}

.logo {
  height: 15px;
}

.content {
  height: 90vh;
  padding-top: 12px;
  background-image: linear-gradient(
    rgba(225, 225, 225, 0),
    rgba(225, 225, 225, 0),
    rgba(225, 225, 225, 0),
    rgba(225, 225, 225, 0.3),
    rgba(225, 225, 225, 0.8),
    rgba(225, 225, 225, 0.9),
    rgba(225, 225, 225, 1)
  );
}

.nav_aria {
  display: flex;
  justify-content: center;
  gap: 4px;
  padding: 12px;
}

.navFirst {
  width: 60px;
  height: 5px;
  border-radius: 5px;
  background-color: #00193533;
}

/* .navFirst:first-child {
  background-color: #001935;
} */

.nav {
  width: 60px;
  height: 5px;
  border-radius: 5px;
  background-color: #00193533;
}

.content_data {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.space {
  height: 300px;
}

.content_data p {
  text-align: center;
  margin-bottom: 20px;
  color: #000d1b99;
  font-weight: 500;
  letter-spacing: -0.8px;
  text-shadow: 5px 5px 5px rgba(255, 255, 255, 0.25);
}

.btn_aria {
  text-align: center;
}

.btn,
.btn:link,
.btn:visited,
.btn:hover,
.btn:active {
  box-sizing: border-box;
  padding: 1.1rem 8rem;
  border-radius: 24px;
  background-color: #001935;
  color: #fff; /*color of your choosing*/
  text-decoration: none;
  font-weight: normal;
  box-shadow: 0px 4px 4px 0px #00000040;
}

.bg1 {
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(
      rgba(225, 225, 225, 1),
      rgba(225, 225, 225, 1),
      rgba(225, 225, 225, 0),
      rgba(225, 225, 225, 0),
      rgba(225, 225, 225, 0),
      rgba(225, 225, 225, 0.3),
      rgba(225, 225, 225, 0.8)
    ),
    url("./../images/splash-bg/bg1.jpg");
